/* ==================== Media queries ======================= */
/*==========  Desktop First Method  ==========*/

@media only screen and (max-width : 1919px) {

  .top-section {
    padding-top: 50px;
  }
  .map-link {
    margin-bottom: 20px;
  }
  .feat-box {
    padding-top: 32px;
    padding-left: 16%;
    padding-right: 115px;
    justify-content: flex-end;
  }
  .feat {
    font-size: 25px;
    margin-bottom: 20px;
    height: 250px;
    width: 290px;
  }
  .feat > span {
    top: 155px;
  }
  .feat > span:before {
    width: 140px;
  }
  .feat img {
    width: 190px;
    height: 190px;
  }

} /*max 1919*/


@media only screen and (max-width : 1439px) {
  .main-title, 
  .name,
  .site-cost,
  .duration {
    font-size: 80px;
  }
  .date, 
  .arena {
    font-size: 50px;
  }
  .site-buy,
  .r-side ul,
  .prep-r,
  .more-subheading {
    font-size: 30px;
  }
  .all-night {
    font-size: 38px;
    padding-top: 0;
    line-height: 1.1;
  }
  .site-cost {
    line-height: 1.1;
  }
  .prepare-section {
    padding-top: 27%;
  }
  .prepare-section .sect-heading,
  .more-section .sect-heading {
    font-size: 60px;
  }
   

} /*max 1439*/



@media only screen and (max-width : 1365px) {
  body {
    font-size: 16px;
  }
  .feat-box {
    padding-left: 0;
  }
  .l-empty:before, 
  .l-side {
    // flex-basis: 40%;
  }
  .button {
    width: 260px;
    font-size: 24px;
    height: 46px;
    line-height: 46px;
  }
  .button-transparent {
    width: 260px;
    font-size: 24px;
    height: 46px;
    line-height: 38px;
  }
  .limited:before,
  .limited br {
    display: none;
  }
  .curr-left-wr {
    padding-left: 40%;
  }
  // .main-title, 
  // .name, 
  // .site-cost, 
  // .duration {
  //   font-size: 50px;
  // }
  // .date, 
  // .arena {
  //   font-size: 35px;
  // }
} /*max 1365*/

@media only screen and (max-width : 1199px) {
  .feat-section {
    padding-top: 240px;
    background-position: 100% 0;
  }
  .obj-bg {
    display: none !important;
  }
  .about-section {
    background-image:url(../img/bg6.svg);
    background-size: cover;
    background-repeat: no-repeat;
  }
  .feat-section {
    padding: 20.8% 0 21%;
    background-image:url(../img/bg2.svg);
    background-size: cover;
    background-repeat: no-repeat;
    margin-top: -25%;
    z-index: 3;
  }
  .top-section-row {
    flex-wrap: wrap;
  }
  .hours-block {
    min-width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  .all-night {
    margin-bottom: 14px;
  }
  .time-row {
    margin: 0 auto;
    max-width: 540px;
  }
  .all-night,
  .footnote {
    padding-left: 0;
  }
  .feat-section {
    padding-right: 80px;
  }
  .feat-box {
    justify-content: space-around;
    padding-right: 0;
  }
  .feat {
    font-size: 18px;
  }
  .top-section {
    text-shadow: 1px 1px 2px #500430, -1px -1px 2px #500430;
  }

  .prep-l {
    flex-basis: 320px;
  }
  .prep-r li {
    line-height: 1.2;
    span {
      display: inline-block;
      line-height: 1.1;
      margin-top: 8px;
    }
  }
  .all-night,
  .prepare-section .sect-heading,
  .more-section .sect-heading,
  .limited-text,
  .sect-heading {
    font-size: 38px;
  }
  .time-row {
    font-size: 24px;
  }
  .l-lined.kassa-buy {
    padding-top: 14px;
    padding-bottom: 14px;
    font-size: 50px;
  }
  .top-btn-wr:before {
    display: none;  
  }
  .sliders-block,
  .sliders-section .sect-heading {
    max-width: 80%; 
  }
  .slider-lg-wr {
    margin-bottom: 0;  
  }
  .slider-sm {
    margin-bottom: 10px;  
  }

} /*max 1199*/

@media only screen and (max-width : 1050px) {
  .prepare-section {
    margin-top: -80px;
    padding-top: 210px;
    z-index: 6;
  }
  .about-section {
    margin-top: -31%;
  }
} /*max 1050*/

@media only screen and (max-width : 991px) {

} /*max 991*/

@media only screen and (max-width : 767px) {

  body {
    font-size: 14px;
  }
  .prep-divided {
    flex-wrap: wrap;
  }
  .prep-l {
    flex-basis: 100%;
    padding-right: 0;
    text-align: left;
    margin-bottom: 30px;
    br {
      display: none;
    }
  }
  .l-lined,
  .prep-r {
    padding-left: 10px;
  }
  .site-buy, 
  .r-side ul, 
  .prep-r,
  .more-subheading {
    font-size: 24px;
  }
  .map-link {
    font-size: 15px;
  }
  .footnote {
    font-size: 14px;
  }
  .all-night, 
  .prepare-section .sect-heading, 
  .more-section .sect-heading, 
  .limited-text, 
  .sect-heading {
    font-size: 24px;
  }
  .l-lined,
  .feat {
    font-size: 14px;
  }
  .feat {
    width: 170px;
    height: 140px;
  }
  .feat > span {
    top: 95px;
    left: 50px;
    padding-top: 5px;
  }
  .feat > span:before {
    width: 80px;
  }
  .feat img {
    width: 120px;
    height: 120px;
  }
  .l-empty:before {
    display: none;
  }
  .site-buy {
    flex-wrap: wrap;
    text-align: left;
    br {
      display: none;
    }
  }
  .site-text,
  .site-cost,
  .duration {
    text-align: left;
    margin-bottom: 10px;
    margin-left: 0;
    padding-left: 0;
    left: 0;
  }
  .divided {
    flex-wrap: wrap;

  }
  .r-side {
    min-width: 100%;
    order: -1;
    margin-bottom: 26px;  
  }
  .btn-block {
    min-width: 100%;  
  }
  .prep-r li {
    margin-bottom: 20px;  
  }
  .slider-controls {
    font-size: 26px;
  }

  .map-heading-cont {
    position: static;
    width: 100%;
    height: auto;
    padding: 10px;
    font-size: 16px;
    background-image: url(../img/elbg.svg);
    background-position: 50% 50%;
    background-size: contain;
  }
  .more-divided {
    flex-wrap: wrap;
  }
  .more-l, 
  .more-r {
    min-width: 100%;

  }
  .more-r {
    border-left: none;
    border-top: 1px solid $red;
    margin-top: 30px;
  }
  .countdown-title {
    font-size: 16px;
  }
  .curr-left-wr {
    font-size: 60px;
  }
  .heading-wr {
    padding-top: 100px;
    padding-bottom: 15px;
    margin-bottom: 0;
  }
  .more-section .sect-heading {
    margin-bottom: 22px;
  }
  .l-lined.kassa-buy {
    margin-bottom: 0;
  }
  .about-section {
    background-position: 0 0;
  }
  .feat-section {
    padding-bottom: 260px;
  }
  .about-section {
    margin-top: -340px;
    padding-top: 110px;
  }
  .figure,
  .line {
    display: none !important;
  }
  .more-r:before {
    display: none;
  }
} /*max 767*/

@media only screen and (max-width : 480px) {
  .date{
    margin-bottom: 20px;
  }
  .all-night {
    margin-top: 20px;
  }
  .map-link {
    margin: 20px 0 50px;
  }
  .feat-section {
    padding-top: 170px;
    padding-right: 0;
  }

  .feat-section .sect-heading,
  .feat-section .l-lined {
    padding-right: 60px;
  }

  .site-buy, 
  .r-side ul, 
  .prep-r {
    font-size: 20px;  
  }

  .more-subheading {
    font-size: 24px;
  }
  .curr-left-wr {
    font-size: 44px;
  }
  .left-text {
    font-size: 20px;
  }
  .heading-wr {
    padding-top: 40px;
  }
} /*max 480*/


@media only screen and (max-width : 319px) {
  .edge-text {
    display: none;
  }
  .l-lined.kassa-buy {
    font-size: 37px;
  }
  .main-title, .name, .site-cost, .duration {
    font-size: 34px;
  }
  .date, .arena {
    font-size: 26px;
  }
  .desc {
    font-size: 16px;
  }
} /*max 319*/


@media only screen and (min-width : 992px) {
  .time-row:after {
    max-width: 0;
    transition: all 1s .8s;
  }
  .time-row.shown:after {
    max-width: 100%;
  }
  .feat > span:before {
    max-width: 0;
    transition: all 1s;
  }
  .feat > span.shown:before {
    max-width: 100%;
  }
  .l-lined:before,
  .left-text:before {
    height: 0;
    top: 50%;

    transform: translateY(-50%);
    transition: all 1s;
  }
  .l-lined.shown:before,
  .left-text.shown:before {
    height: 100%;
  }
  .sliders-section .sect-heading:after,
  .slider-controls:before,
  .more-subheading:after {
    max-width: 0;
    transition: all 1s;
  }
  .sliders-section .sect-heading.shown:after,
  .slider-controls.shown:before,
  .more-subheading.shown:after {
    max-width: 100%;
  }
  .prep-r:before,
  .more-r:before {
    max-height: 0;
    transition: all 1s;
  }
  .prep-r.shown:before,
  .more-r.shown:before {
    max-height: 100%;
  }
}

// /*==========  intervals  ==========*/

@media only screen and (min-width : 320px) and (max-width : 1920px) {
    .prep-edge {
      font-size: calc(7.88vw + 8.8px);
      top: calc(0.81vw + 124px);
    }
    .feat-edge {
      font-size: calc(8.5vw + 16.8px);
     
    }
}

@media only screen and (min-width : 1921px) and (max-width : 3000px) {
    .prep-edge {
      top: calc(4.17vw + 58.9px);
    }
}


@media only screen and (min-width : 1365px) and (max-width : 1919px) {
  .feat-edge {
     font-size: calc(1.8vw + 105.4px);
     top: calc(13vw + 811px);
  }
}


@media only screen and (min-width : 320px) and (max-width : 1365px) {
  .feat-edge {
     top: calc(57.47vw + 190px);
  }
  .main-title, 
  .name, 
  .site-cost, 
  .duration {
    font-size: calc(4.31vw + 21.2px);
  }
  .date, 
  .arena {
    font-size: calc(2.39vw + 17.3px);
  }
  .desc {
    font-size: calc(.77vw + 13.6px);
  }

}

@media only screen and (min-width : 480px) and (max-width : 1199px) {
  .feat-section {
    padding-top: 240px;
    margin-top: calc(-17vw - 45.4px);
    
    background-position: 98% 0;
  }
}

@media only screen and (min-width : 320px) and (max-width : 767px) {
  .l-lined.kassa-buy {
    font-size: calc(2.9vw + 27px);
  }
}



