
/* ==================== common styles ======================= */

html {
  overflow-x: hidden;
}

body {
  min-width: 320px;
  max-width: 3000px;
  overflow-x: hidden;
  color: #fff;
  font-family: $rr;
  font-size: 18px;
  line-height: 1.2;
  margin: 0 auto;
  background-color: #260F51;
}

.figure {
  display: block;
  position: absolute;
  max-width: 300px;

}

.figure img {
  position: relative;
  width: 100%;
  height: 100%;
  transition: all 3s cubic-bezier(0.53, 1.9, 0.35, 0.89);

}



*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  top: -200px;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(15,0,15, .8);
  z-index: 20;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  display: inline-block;
  background: none;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
}

img {
  max-width: 100%;
}

input[type="file"] {
  display: none;
}

img,
input,
input:focus {
  outline: none;
  border: none;
}



input,
textarea {
  &::-ms-clear {
    display: none;
  }
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

select::-ms-expand {
    display: none;
}

textarea {
  display: block;
  resize: none;

  overflow: hidden;
  border: none;
  outline: none;
}

section {
  position: relative;
  min-height: 400px;
  background-position: 50% 0;
}

.wrapper {
  width: 96%;
  max-width: 1260px;
  margin: 0 auto;


  position: relative;
  z-index: 1;
}

.button {

  display: block;
  position: relative;
  width: 400px;
  height: 56px;
  font-family: $rb;
  font-size: 30px;
  letter-spacing: .5px;
  
  text-align: center;
  text-transform: uppercase;
  text-shadow: none !important;
  color: #fff;
  text-align: center;
  line-height: 56px;
  transition: all .4s;
  padding: 0 8px;
  margin: 10px;
  cursor: pointer;
  z-index: 1;
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: -10px;
    bottom: -10px;
    left: -10px;
    right: -10px;
    border: 4px solid #37D800;
    z-index: 0;
  }

  >span {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    z-index: -1;
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(90deg, #98DB0B, #00A316, #98DB0B);
      width: 200%;
      transition: all .4s;
    }
  }
}

.button:hover {
  >span:before {
    right: -100%;
  }
}

