.time-bit {
  position: relative;
  width: 90px;
  font-size: 54px;
  padding: 0 10px;
  &:after {
    content: ":";
    position: absolute;
    top: 14px;
    right: -3px;
    font-family: $mm;
    font-size: .7em;
    animation: pulse 1s infinite;
  }
  &:last-of-type {
    &:after {
      display: none;
    }
  }
}


@keyframes pulse {
  0% {opacity: 0;}
  20% {opacity: 0;}
  50% {opacity: 1;}
  75% {opacity: 0;}
  100% {opacity: 0;}
}

