

/*modal window*/


.modal-cont {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-height: 98vh;
  overflow-y: auto;
  width: 700px;
  max-width: 96%;
  padding: 30px 20px 100px;
  color: $lime;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 21;
}

.modal-top {
  text-align: right;
  padding-right: 10px;
  margin-bottom: 4px;
}

.modal-logo {
  display: block;
  max-width: 120px;
}

.modal-close {
  font-size: 36px;
  color: $lime;

  cursor: pointer;
}

.modal-form {
  display: flex;
  flex-wrap: wrap;
  >div {
    outline: 1px solid rgba(0,255,255,.1);
  }
}

.form-l,
.form-r {
  flex: 1 1 50%;
  padding: 10px 7px;
}

.form-bottom {
  min-width: 100%;
  padding: 20px 0;
}

.modal-form input[type="text"],
.modal-form input[type="email"],
.modal-form input[type="tel"] {
  height: 48px;
  line-height: 44px;
  padding: 0 10px;
  margin-bottom: 14px;
  &:last-child {
    margin-bottom: 0;
  }
}

.modal-form input[type="text"],
.modal-form input[type="email"],
.modal-form input[type="tel"],
.modal-form textarea {
  width: 100%;
  max-width: 100%;
  font-family: RobotoCondencedBold;
  font-size: 16px;
  color: $lime;
  border: 1px solid $lime;
  background-color: rgba(10,0,10,.5);
  transition: all .4s;
  &:focus {
    background-color: rgba(10,0,10,.7);
  }
}

.modal-form textarea {
  height: 100%;
  padding: 12px 10px;
}

.textarea-wr {
  height: 100%;
}


.modal-form .button {
  width: 320px;
  height: 36px;
  line-height: 36px;
  font-size: 22px;
  margin: 0 auto;

}


.modal-form ::-webkit-input-placeholder {
  font-family: RobotoCondencedBold;
  font-size: 16px;
  color: $lime;
  opacity: .6;
}

.modal-form ::-moz-placeholder {
  font-family: RobotoCondencedBold;
  font-size: 16px;
  color: $lime;
  opacity: .6;
}

.modal-form :-ms-input-placeholder {
  font-family: RobotoCondencedBold;
  font-size: 16px;
  color: $lime;
  opacity: .6;
}

.modal-form ::placeholder {
  font-family: RobotoCondencedBold;
  font-size: 16px;
  color: $lime;
  opacity: .6;
}

@media only screen and (min-width: 768px) {
  .modal-cont {
    background-image:url(../img/elbg2.svg);
  }
}/*>=768px;*/

@media only screen and (max-width: 767px) {
  .modal-cont {
    width: 96%;
    max-width: 400px;
    background-position: 50% 0;
    background-image:url(../img/elbg3.svg);
    background-size: auto 100%;
    padding-bottom: 70px;
  }
  .form-l, .form-r {
    flex-basis: 100%;
    min-width: 100%;
    padding-top: 7px;
  }
  .modal-form .button {
    width: 240px;
  }
  .modal-form input[type="text"], 
  .modal-form input[type="email"], 
  .modal-form input[type="tel"] {
    height: 40px;
    line-height: 38px;

  }
  .modal-form textarea {
    min-height: 50px; 
    height: 80px;
    resize: vertical;
    padding-top: 8px;
    padding-bottom: 4px;
  }


}/*<=767px;*/

